

















import Vue from 'vue'
import { mapper } from '../store'

export default Vue.extend({
  computed: {
    ...mapper.mapGetters(['svgs', 'is_mobile']),
    ...mapper.module('toast').mapState(['labels']),
  },
  methods: {
    ...mapper.module('toast').mapMutations(['removeLabel']),
    clickClose() {
      this.removeLabel({ id: this.labels[0].id })
    },
  },
})
